.feed{
    flex:0.6;
    margin :0 20px;
}
.feed__inputcontainer{
    background-color:white;
    padding:10px;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-radius: 10px;
}
.feed__input{
    border:1px solid lightgray;
    border-radius:30px;
    padding:10px;
    padding-left: 15px;
   color:gray;
   display: flex;
}
.feed__input>form{
    display:flex;
    width:100%;
}
.feed__input > form>input{
    flex: 1;
    border:none;
    outline-width:0;
    margin-left:10px;
    font-weight: 600;
}
.feed__inputOptions{
    display:flex;
    justify-content:space-evenly
}