.login{
    display: grid ;
    margin-left: auto ;
    margin-right:auto;
    padding-top: 100px ;
    padding-bottom: 100px;
    place-items: center;
}
.login >img{
    height:100px;
    margin-top:20px;
    margin-bottom: 5px;
    object-fit:contain;
    width:300px
}
.login > form{
    display:flex;
    flex-direction:column;
}
.login> form >input{
width:350px;
height:50px;
font-size: 20px;
padding-left: 10px;
margin-bottom: 10px;
border-radius: 5px;
}
.login>form>button{
    width:365px;
    height:50px;
    font-size:large;
    color:white;
    background-color :#0074B1;
    border-radius: 5px;
}
.login__register{
    color :#0177b7;
    cursor:pointer;
}